.graph {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 2rem;
    gap: 2rem;
}

.graphItem {
    box-shadow: 0 10px 20px rgb(0,0,0,0.1);
    border-radius: 0.2rem;
    display: grid;
    align-items: center;
}

.total {
    grid-column: 1/3;
    padding: 2rem;
    font-size: 2rem;
}

@media (max-width: 40rem) {
    .graph {
        grid-template-columns: 1fr;
    }
    .total {
        grid-column: 1;
    }
}